.nav {
    display: flex;
    align-items: center;
    position: sticky;
    height: 50px;
    width: 100%;
    top: 0;
    background-color: rgb(24, 185, 161);
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14),
    0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.navLeft {
    flex: 3;
}

.logo {
    font-size: 24px;
    margin-left: 20px;
    font-weight: bold;
    color: white;
    cursor: pointer;
}

.navCenter {
    flex: 5;
}

.searchBar {
    display: flex;
    align-items: center;
    width: 100%;
    height: 30px;
    background-color: white;
    border-radius: 30px;
}

.searchIcon {
    font-style: 20px;
    margin-left: 10px;
    margin-right: 5px;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
}

.searchIcon:hover {
    opacity: 1;
}

.searchInput {
    border: none;
    width: 70%;
}

.searchInput:focus {
    outline: none;
}


.navRight {
    flex: 4;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;
}

.navLinks {
    display: flex;
    align-items: center;
}

.navLink {
    margin-right: 15px;
    font-size: 14px;
    cursor: pointer;
}

.navIcons {
    display: flex;
}

.navIconItem:first-child {
    margin-left: 80px;
}

.navIconItem {
    position: relative;
    margin-right: 15px;
    cursor: pointer;
}

.navIconItem:last-child {
    margin-right: 80px;
}

.navIconBadge {
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
    color: white;
    top: -5px;
    right: -5px;
}

.picture {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
}